import { BrowserRouter, Route, Switch } from "react-router-dom";
import ToDoList from "./routes/ToDoList";

function Router() {
  return (
    <BrowserRouter basename="/react-manegement">
      <Switch>
        <Route path="/">
          <ToDoList />
        </Route>
      </Switch>
    </BrowserRouter>
  );
}

export default Router;
