import React, { useState } from "react";
import { useRecoilState, useRecoilValue } from "recoil";
import { IToDo, toDoState, categoriesState } from "../atoms";

function ToDo({ text, category, id }: IToDo) {
  const [toDos, setToDos] = useRecoilState(toDoState);
  const onClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    const {
      currentTarget: { name },
    } = event;
    setToDos((oldToDos) => {
      const targetIndex = oldToDos.findIndex((toDo) => toDo.id === id);
      const newToDo = { text, category: name as IToDo["category"], id };
      const newToDos = [
        ...oldToDos.slice(0, targetIndex),
        newToDo,
        ...oldToDos.slice(targetIndex + 1),
      ];

      localStorage.setItem("toDos", JSON.stringify(newToDos));
      return newToDos;
    });
  };

  const onDelete = (event: React.MouseEvent<HTMLButtonElement>) => {
    const storedToDos = JSON.parse(localStorage.getItem("toDos") || "[]");
    const updatedToDos = storedToDos.filter((toDo: IToDo) => toDo.id !== id);
    localStorage.setItem("toDos", JSON.stringify(updatedToDos));
    setToDos(updatedToDos);
  };
  const categories = useRecoilValue(categoriesState);
  return (
    <li>
      <span>{text}</span>
      {categories.map((cat) => {
        if (cat !== category && cat !== "DELETE" && category !== "TRASH") {
          return (
            <button key={cat} name={cat} onClick={onClick}>
              {cat}
            </button>
          );
        }
        return null;
      })}
      {category === "TRASH" && (
        <button
          style={{ backgroundColor: "blue" }}
          name="TO_DO"
          onClick={onClick}
        >
          TO_DO
        </button>
      )}
      {category === "TRASH" && (
        <button
          style={{ backgroundColor: "red" }}
          name="DELETE"
          onClick={onDelete}
        >
          Delete
        </button>
      )}
    </li>
  );
}

export default ToDo;
