import { atom, selector } from "recoil";

export const defaultCategories = ["TO_DO", "DOING", "DONE", "TRASH", "DELETE"];

export const dynamicCategoriesState = atom<string[]>({
  key: "dynamicCategories",
  default: [], // 초기에는 빈 배열
});

export enum Categoried {
  "TO_DO" = "TO_DO",
  "DOING" = "DOING",
  "DONE" = "DONE",
  "TRASH" = "TRASH",
  "DELETE" = "DELETE",
}

export interface IToDo {
  text: string;
  id: number;
  category: string;
}
export const categoryState = atom<string>({
  key: "category",
  default: defaultCategories[0],
});

export const toDoState = atom<IToDo[]>({
  key: "toDo",
  default: [],
});

export const categoriesState = atom<string[]>({
  key: "categories",
  default: Object.values(Categoried),
});

export const toDoSelector = selector({
  key: "toDoSelector",
  get: ({ get }) => {
    const toDos = get(toDoState);
    const category = get(categoryState);
    return toDos.filter((toDo) => toDo.category === category);
  },
});
